@import url(https://fonts.googleapis.com/css?family=Lato:100,300,400,700&display=swap);
/* Colors.
 ------------------------------------------------- */
/* Page template.
-------------------------------------------------- */
html {
  min-height: 100%;
  position: relative; }

body {
  color: #343A40;
  font-family: Lato, 'Helvetica Neue', Arial, Helvetica, sans-serif;
  font-size: 14px;
  margin: 0; }
  body .segment {
    padding: 8rem 4rem; }

h1 {
  font-size: 2rem;
  font-weight: 400; }

/* Scrollbar.
 ------------------------------------------------- */
::-webkit-scrollbar {
  background-color: #F5F5F5;
  height: 8px;
  width: 8px; }

::-webkit-scrollbar-thumb {
  background-color: #CBCBCB; }

::-webkit-scrollbar-thumb:hover {
  background-color: #BABABA; }

::-webkit-scrollbar-track {
  background-color: #F5F5F5; }

/* Selection.
 ------------------------------------------------- */
::-moz-selection {
  background: #42C17E; }
::selection {
  background: #42C17E; }

/* Colors.
 ------------------------------------------------- */
/* Header component.
-------------------------------------------------- */
.header .hero-content {
  display: flex;
  flex-direction: column;
  position: relative; }
  .header .hero-content .banner {
    height: 50%; }
  .header .hero-content .sub-text {
    color: #FFFFFF;
    font-size: 1.6rem; }

/* Bio component.
-------------------------------------------------- */
.bio {
  display: flex;
  min-height: 300px; }
  .bio .left {
    padding-right: 40px;
    width: 60%; }
    .bio .left .section:first-child {
      margin-bottom: 50px; }
    .bio .left .section .title {
      align-items: center;
      display: flex;
      flex-direction: row; }
      .bio .left .section .title svg {
        font-size: 1.8rem;
        margin-right: 10px; }
    .bio .left .section .paragraph {
      font-size: 1.2rem;
      line-height: 1.8rem; }
  .bio .right {
    display: flex;
    justify-content: center;
    padding-left: 40px;
    width: 40%; }
    .bio .right .portrait {
      border-radius: 10px;
      height: 100%; }

